import React from 'react';
import LinkExternal from './LinkExternal';
import { EtherscanLinkProps } from './types';
import { appChain } from '../../constants';



const EtherscanLink: React.FC<EtherscanLinkProps> = ({ address, token, tx, children, ...props }) => {
  let href = '';
  switch (appChain) {
    case 4: href = 'https://rinkeby.etherscan.io'; break;
    case 56: href = 'https://bscscan.com'; break;
    default: break;
  }

  if (address) {
    href += `/address/${address}`;
  } else if (token) {
    href += `/token/${token}`;
  } else if (tx) {
    href += `/tx/${tx}`;
  }

  return (
    <LinkExternal href={href} {...props}>
      {children}
    </LinkExternal>
  );
};

export default EtherscanLink;
