import React, { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';
import { NavLink, Link } from 'react-router-dom';
import { useModal } from 'react-simple-modal-provider';
import { routerPaths } from '../router/helper';
import { appName, appChain } from '../constants';
import { useEagerConnect, useActiveWeb3React } from '../hooks/index';
import { Container, Icon, Menu, Segment, Sidebar } from 'semantic-ui-react';



function HeaderMobile({ children }) {
  const [sidebarOpened, setSidebarOpened] = useState(false);

  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { chainId, account, active } = context;

  const { open: openWalletConnectModal } = useModal('WalletConnectModal');
  const { open: openWalletAccountModal } = useModal('WalletAccountModal');
  const accountShort = account
    ? `${account.substring(0, 6)}...${account.substring(account.length - 4)}`
    : 'Connect wallet';


  return (
    <Sidebar.Pushable>
      <Sidebar
        as={Menu}
        animation='overlay'
        inverted
        onHide={() => setSidebarOpened(false)}
        vertical
        visible={sidebarOpened}
      >
        <Menu.Item as={NavLink} to={routerPaths.heroes}>Heroes</Menu.Item>
        <Menu.Item as={NavLink} to={routerPaths.items}>Items</Menu.Item>
        <Menu.Item as={NavLink} to={routerPaths.mixer}>Mixer</Menu.Item>
        <Menu.Item as={NavLink} to={routerPaths.marketplace}>Marketplace</Menu.Item>
        <Menu.Item as={NavLink} to={routerPaths.lottery}>Lottery</Menu.Item>

        <Menu.Item as={NavLink} to={routerPaths.staking}>Staking</Menu.Item>
        <Menu.Item as={NavLink} to={routerPaths.farming}>Farming</Menu.Item>
      </Sidebar>

      <Sidebar.Pusher dimmed={sidebarOpened}>
        <Segment
          as={'header'}
          inverted
          textAlign='center'
          vertical
        >
          <Menu fixed='top' size='large' pointing inverted>
            <Container>
              <Menu.Item onClick={() => setSidebarOpened(true)}>
                <Icon name='sidebar' />
              </Menu.Item>
              <Menu.Item as={Link} to={routerPaths.main}>{appName}</Menu.Item>

              <Menu.Item
                position='right'
                style={{ color: chainId === appChain ? 'green' : 'red' }}
                onClick={(triedEager && active && account)
                  ? openWalletAccountModal
                  : openWalletConnectModal
                }
              >{accountShort}</Menu.Item>
            </Container>
          </Menu>
        </Segment>

        {children}
      </Sidebar.Pusher>
    </Sidebar.Pushable>
  );
};

export default hot(module)(HeaderMobile);
